import React from 'react';
import classNames from 'classnames';
import { SectionSplitProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';
import Image from '../elements/Image';
import ButtonGroup from '../elements/ButtonGroup';
import Button from '../elements/Button';

const propTypes = {
  ...SectionSplitProps.types
}

const defaultProps = {
  ...SectionSplitProps.defaults
}

const FeaturesSplit = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {

  const outerClasses = classNames(
    'features-split section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-split-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const splitClasses = classNames(
    'split-wrap',
    invertMobile && 'invert-mobile',
    invertDesktop && 'invert-desktop',
    alignTop && 'align-top'
  );

  const sectionHeader = {
    title: 'Highlights',
    paragraph: 'We participated in several local and international competitions.'
  };

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={splitClasses}>

            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-left" data-reveal-container=".split-item">
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                22 MAR 2022
                  </div>
                <h3 className="mt-0 mb-12">
                  1st Place in CyberZee'22
                  </h3>
                <p className="m-0">
                Our team won 1st place🏆 in the CyberZee’22,

An inter-university cyber security quiz competition organized by the ISACA students group of the University of Kelaniya.<br/>  <br/>
#CyberZee22 #cybersecurity #ISACA #NSBM #RuntimeTerrorLK
                  </p>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/cyberzee22.jpg')}
                  alt="Features split 01"
                  width={528}
                  height={396} />
              </div>
            </div>

            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-right" data-reveal-container=".split-item">
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  14 FEB 2022
                  </div>
                <h3 className="mt-0 mb-12">
                1st Place in SLIIT CodeFest CTF
                  </h3>
                <p className="m-0">
                We won 1st place in the SLIIT Codefest - "Hack Me If You Can" CTF Competition. <br/>  <br/>
                 #teamwork #cybersecurity #ctf #RuntimeTerrorLK
                  </p>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/cf2021.jpg')}
                  alt="Features split 02"
                  width={528}
                  height={396} />
              </div>
            </div>

            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-left" data-reveal-container=".split-item">
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  12 DEC 2021
                  </div>
                <h3 className="mt-0 mb-12">
                  1st Place in <br/>iCS HackTheWorld 2.0
                  </h3>
                <p className="m-0">
                " After a phenomenal battle, we finally have our winners.
Presenting to you, the Winners of this CTF challenge, whose dedication for a continuous 24 hours made them the champions of the iCS 2.0.
Congratulations to the winning team, Team ISACA NSBM, 1st Runners Up, Team Cypher, and 2nd Runners Up Team Spark and wish you all the good fortune in your future endeavours.
We would like to extend our heartiest congratulations to all the participants and thank everyone for their efforts.
#UCSCISG #UCSC #iCS " 
<br/><br/>
- UCSC ISACA Student Group
                  </p>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/ics2.0.jpg')}
                  alt="Features split 03"
                  width={528}
                  height={396} />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-xs" style={{alignContent:"center"}}>
             <center> <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
                ....And there's more. Visit Achievements.
                </p>
              </center>
              <div className="reveal-from-bottom" data-reveal-delay="600">
              <center>
                  <Button tag="a" color="primary" style={{alignContent:"center"}} wideMobile href="/achievements">
                    Achievements
                    </Button>
                </center>
                              </div>
            </div>
            <div>
              <br/>
              <br/>
            </div>
    </section>
  );
}

FeaturesSplit.propTypes = propTypes;
FeaturesSplit.defaultProps = defaultProps;

export default FeaturesSplit;