import React from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';
import Button from '../elements/Button';

const propTypes = {
  ...SectionTilesProps.types
}

const defaultProps = {
  ...SectionTilesProps.defaults
}

const Testimonial = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {

  const outerClasses = classNames(
    'testimonial section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'testimonial-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const tilesClasses = classNames(
    'tiles-wrap',
    pushLeft && 'push-left'
  );

  const sectionHeader = {
    title: 'Blogs',
    paragraph: 'We write articles about Exploits Development, Bug Bounty, Linux, Networking, and many more related to Cyber Security.  '
  };

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>

            <div className="tiles-item reveal-from-right" data-reveal-delay="200">
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                    — View version information show version Enable root en Global config mode conf t Setting a password enable secret password Setting a banner banner motd # Setup a static route ip route...
                      </p>
                </div>
                <div className="testimonial-item-footer text-xs mt-32 mb-0 has-top-divider">
                  <span className="testimonial-item-name text-color-high">z9fr</span>
                  <span className="text-color-low"> / </span>
                  <span className="testimonial-item-link">
                    <a href="https://z9fr.github.io/post/switch-configuration/">Basic Switch Configuration</a>
                  </span>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                    — WSL (Windows Subsystem for Linux) has been a real game changer for the last few years. Even though it has its issues it can be recommended for hacking and pentesting. #Getting Started you can follow...
                      </p>
                </div>
                <div className="testimonial-item-footer text-xs mt-32 mb-0 has-top-divider">
                  <span className="testimonial-item-name text-color-high">rvz</span>
                  <span className="text-color-low"> / </span>
                  <span className="testimonial-item-link">
                    <a href="https://rvizx.github.io/blog/wsl-for-hacking">WSL for Hacking</a>
                  </span>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-left" data-reveal-delay="200">
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                    — Intigriti 0821 XSS Challenge by @WHOISbinit Website Overview Let’s take a look at the website and how it works 👀 The main page is using a iframe and looks like main page don’t have any details...
                      </p>
                </div>
                <div className="testimonial-item-footer text-xs mt-32 mb-0 has-top-divider">
                  <span className="testimonial-item-name text-color-high">isira</span>
                  <span className="text-color-low"> / </span>
                  <span className="testimonial-item-link">
                    <a href="https://blog.isiraadithya.com/intigriti-0821-xss-challenge-writeup/">Intigriti 0821 XSS Challenge</a>
                  </span>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      <div className="container-xs" style={{alignContent:"center"}}>
             <center> <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
             The more you read, the better you hack, <br/>Visit blogs to see all the blog posts by RuntimeTerror..
                </p>
              </center>
              <div className="reveal-from-bottom" data-reveal-delay="600">
              <center>
                  <Button tag="a" color="primary" style={{alignContent:"center"}} wideMobile href="/">
                    Blogs
                    </Button>
                </center>
                              </div>
            </div>
            <div>
              <br/>
              <br/>
            </div>
    </section>
  );
}

Testimonial.propTypes = propTypes;
Testimonial.defaultProps = defaultProps;

export default Testimonial;